:root {
  --primary-text-color: #000;
  --accent-color: #e06717;
  --primary-background-color: #fff;
  --active-yellow: #fcc412;
  --hover-yellow: #fcc000;
  --active-green: #0dcd2a;
  --hover-green: #077c3c;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

button {
  width: 120px;
  height: 40px;
  cursor: pointer;
  background-color: #fcc000;
  border: 2px solid #960505;
  border-radius: 5px;
  padding: 0;
  font-family: inherit;
}

button:hover {
  background-color: var(--accent-color);
}

address {
  font-style: normal;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.hidden {
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body {
  background-color: var(--primary-background-color);
  color: var(--primary-text-color);
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

a {
  color: inherit;
}

a:hover {
  color: #00f;
}

.no-scroll {
  overflow: hidden;
}

.current {
  color: var(--accent-color);
}

.list {
  grid-template-columns: repeat(4, 1fr);
  justify-items: center;
  gap: 80px 20px;
  padding: 20px;
  display: grid;
}

.list-item {
  width: 100%;
  max-width: 300px;
  text-align: center;
  border: 2px solid var(--accent-color);
  background-color: var(--primary-background-color);
  border-radius: 10px;
  padding: 30px;
}

.list-img {
  width: 100%;
}

.list-title {
  margin-top: 10px;
  margin-bottom: 10px;
}

.list-text {
  margin-top: 20px;
}

.list-link {
  margin-bottom: 10px;
  font-size: 18px;
}

.list-btn {
  justify-content: center;
  gap: 20px;
  margin-top: 10px;
  display: flex;
}

.modal-item {
  width: 600px;
  background-color: #ada861;
  padding: 14px 20px;
}

.basicLightbox {
  width: 100%;
  height: 100vh;
  opacity: .01;
  z-index: 1000;
  will-change: opacity;
  background: rgba(0, 0, 0, .8);
  justify-content: center;
  align-items: center;
  transition: opacity .4s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.basicLightbox--visible {
  opacity: 1;
}

.basicLightbox__placeholder {
  max-width: 100%;
  z-index: 1;
  will-change: transform;
  transition: transform .4s;
  transform: scale(.9);
}

.basicLightbox__placeholder > iframe:first-child:last-child, .basicLightbox__placeholder > img:first-child:last-child, .basicLightbox__placeholder > video:first-child:last-child {
  max-width: 95%;
  max-height: 95%;
  margin: auto;
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.basicLightbox__placeholder > iframe:first-child:last-child, .basicLightbox__placeholder > video:first-child:last-child {
  pointer-events: auto;
}

.basicLightbox__placeholder > img:first-child:last-child, .basicLightbox__placeholder > video:first-child:last-child {
  width: auto;
  height: auto;
}

.basicLightbox--iframe .basicLightbox__placeholder, .basicLightbox--img .basicLightbox__placeholder, .basicLightbox--video .basicLightbox__placeholder {
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.basicLightbox--visible .basicLightbox__placeholder {
  transform: scale(1);
}

